
.circle {
    z-index: 0;
    position: fixed; /* Position it relative to the viewport */
    bottom: -45px; /* Center it horizontally */
    right: -45px; /* Space above the image */
    width: 100px; /* Initial width */
    height: 100px; /* Initial height */
    background-color: #f3f2f5; /* Circle color */
    border-radius: 50%; /* Make it a circle */
    animation: increaseCircleSize 1.2s ease-in-out forwards; /* Animation to increase size */
}

/* Define the size increase animation */
@keyframes increaseCircleSize {
    0% {
        transform: scale(0.1); /* Start at original size */
    }
    100% {
        transform: scale(31.2); /* Increase size to 150% */
    }
}

.circle-container {
    position: fixed;
    width: 100%;
    height: 100vh;
}

.little-circle {
    z-index: 1;
    position: fixed; /* Position it relative to the viewport */
    bottom: 1200px; /* Center it horizontally */
    right: -45px; /* Space above the image */
    width: 300px; /* Initial width */
    height: 300px; /* Initial height */
    background-color: #e8e4f7; /* Circle color */
    border-radius: 50%; /* Make it a circle */
    transform-origin: 200px 1470px;
    animation: reverseRotation 10s linear infinite;
}

@keyframes reverseRotation {
    0% {
        transform: rotate(0deg); /* Start rotation */
    }
    100% {
        transform: rotate(-360deg); /* Complete rotation */
    }
}

.profile-picture {
    position: absolute; /* Set button to absolute */
    max-width: 150px; /* Set a max width */
    height: auto; /* Maintain aspect ratio */
    left: 21%; /* Center it horizontally */
    margin-top: 60px; /* Space above the image */
    border-radius: 100%; /* Optional: add rounded corners */
    border: 5px solid #9d97ad; /* Change color and thickness of the border */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    z-index: 2;
}

.profile-picture:hover {
    border: 5px solid #bebad1;
    animation: rotate 4s linear infinite;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg); /* Start rotation */
    }
    100% {
        transform: rotate(360deg); /* Complete rotation */
    }
}

.home-page {
